import React, { useState } from "react";
import About from "./About";
import Contact from "./Contact";
import LeftBar from "./LeftBar";
import Resume from "./Resume";
import MobileMenu from "./MobileMenu";
import Header from "./Header";
import NavBar from "./NavBar";

// import detectZoom from "detect-zoom";

/**
 * It renders the home page
 * @returns The JSX code for the entire page.
 */
const Home = () => {
  if (
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
      navigator.userAgent
    )
  ) {
    // console.log("DEVICE IS MOBILE");
    // console.log("Zoom", detectZoom.zoom());
    // console.log("device", detectZoom.device());
    document.body.style.zoom = "55%";
  }
  // else {
  //   console.log("DEVICE IS PC");
  //   console.log("Zoom", detectZoom.zoom());
  //   console.log("device", detectZoom.device());
  // }

  const [home, setHome] = useState(true);
  const [onlyAbout, setOnlyAbout] = useState(false);
  const [onlyResume, setOnlyResume] = useState(false);
  const [onlyContact, setOnlyContact] = useState(false);

  return (
    <div id="home" className="App flex h-max bg-[#1e1e1e]">
      <div className="bg-[#262526] fixed h-full hidden lg:block">
        <LeftBar />
      </div>
      <div className="bg-[#1e1e1e] lg:pl-72 h-full ">
        <div className="lg:hidden">
          <MobileMenu />
        </div>
        <div className="bg-[#424042] h-16 w-full fixed hidden lg:block">
          <NavBar
            setHome={setHome}
            setOnlyAbout={setOnlyAbout}
            setOnlyResume={setOnlyResume}
            setOnlyContact={setOnlyContact}
          />
        </div>
        {home ? (
          <>
            <Header id="Header" />
            <About id="About" />
            <Resume id="Resume" />
            <Contact id="Contact" />{" "}
          </>
        ) : onlyAbout ? (
          <About id="About" />
        ) : onlyResume ? (
          <Resume id="Resume" />
        ) : onlyContact ? (
          <Contact id="Contact" />
        ) : null}
      </div>
    </div>
  );
};

export default Home;
